<template>
  <div class="assistant-page">
    <!--  -->
    <div 
      v-show="showSidebar" 
      class="sidebar-overlay"
      @click="toggleSidebar"
    ></div>

    <!--  -->
    <div class="sidebar" :class="{ 'sidebar-hidden': !showSidebar }">
      <!--  -->
      <div class="new-chat-btn" @click="createNewSession">
        <van-button block type="primary" icon="plus"></van-button>
      </div>

      <!--  -->
      <div class="session-list">
        <van-cell
          v-for="session in sessions"
          :key="session.id"
          :title="formatSessionTitle(session)"
          :class="{ active: currentSession === session.id }"
          @click="switchSession(session)"
        >
          <template #icon>
            <van-icon name="chat-o" class="session-icon" />
          </template>
          <template #right-icon>
            <van-icon 
              name="delete-o" 
              class="delete-icon"
              @click.stop="confirmDelete(session)"
            />
          </template>
        </van-cell>
      </div>
    </div>

    <!--  -->
    <div class="chat-main">
      <!--  -->
      <div class="chat-header">
        <van-icon 
          name="bars" 
          class="menu-icon"
          :class="{ 'menu-icon-active': showSidebar }"
          @click="toggleSidebar"
        />
        <span class="session-title">{{ currentSessionTitle }}</span>
      </div>

      <!--  -->
      <div class="message-list" ref="messageList">
        <template v-if="messages.length">
          <div 
            v-for="(message, index) in messages" 
            :key="index"
            :class="['message-item', message.role === 'user' ? 'user-message' : 'assistant-message']"
          >
            <div class="message-content">
              <van-icon 
                :name="message.role === 'user' ? 'manager' : 'service'" 
                class="avatar-icon"
              />
              <div class="message-text">
                <!--  -->
                <template v-if="message.role === 'user'">
                  {{ message.content }}
                </template>
                <!-- AI  Markdown  -->
                <div 
                  v-else 
                  class="markdown-content"
                  v-html="renderMarkdown(message.content)"
                ></div>
              </div>
            </div>
          </div>
        </template>
        <div v-else class="empty-message">
          <van-empty description="" />
        </div>
        <!--  -->
        <div v-if="isLoading" class="message-item assistant-message">
          <div class="message-content">
            <van-icon name="service" class="avatar-icon" />
            <div class="message-text loading">
              <span class="dot"></span>
              <span class="dot"></span>
              <span class="dot"></span>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="input-area">
        <van-field
          v-model="inputMessage"
          type="textarea"
          placeholder="..."
          rows="1"
          autosize
          @keypress.enter.prevent="sendMessage"
        >
          <template #button>
            <van-button 
              size="small" 
              type="primary" 
              :loading="isLoading"
              :disabled="!inputMessage.trim() || isLoading"
              @click="sendMessage"
            >
              
            </van-button>
          </template>
        </van-field>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, nextTick } from 'vue'
import { showToast, showDialog } from 'vant'
import axios from 'axios'
import MarkdownIt from 'markdown-it'
import hljs from 'highlight.js'
import { useRouter } from 'vue-router'

const router = useRouter()

const CHAT_ID = 'ff71490cf4b211efae140242ac130006'
const showSidebar = ref(false)
const sessions = ref([])
const currentSession = ref(null)
const messages = ref([])
const inputMessage = ref('')
const isLoading = ref(false)
const messageList = ref(null)
const currentAnswer = ref(null)

const currentSessionTitle = ref('')

//  axios 
const http = axios.create({
  baseURL: 'https://lgj-8082.colorstoneai.com',
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json'
  },
  //  WebSocket
  httpAgent: false,
  httpsAgent: false
})

//  token
http.interceptors.request.use(config => {
  const token = localStorage.getItem('token')
  if (token) {
    config.headers.Authorization = `Bearer ${token}`
  }
  return config
})

// 
const toggleSidebar = () => {
  showSidebar.value = !showSidebar.value
  // 
  if (showSidebar.value) {
    getSessionList()
  }
}

// 
const getSessionList = async () => {
  try {
    const res = await http.get(`/api/v1/chats/${CHAT_ID}/sessionsList`, {
      params: {
        page: 1,
        pageSize: 30,
        orderby: 'create_time',
        desc: true
      }
    })

    if (res.data.code === 0) {
      const sessionList = res.data.data.data || []
      sessions.value = sessionList.map(session => {
        // 
        let title = ''
        if (session.messages && session.messages.length > 0) {
          const userMessages = session.messages.filter(msg => msg.role === 'user')
          if (userMessages.length > 0) {
            title = userMessages[0].content
          }
        }
        
        return {
          id: session.id,
          title: title,
          createTime: session.create_time,
          messages: session.messages || []
        }
      })

      // 
      if (sessions.value.length > 0) {
        const firstSession = sessions.value[0]
        currentSession.value = firstSession.id
        currentSessionTitle.value = firstSession.title
        messages.value = firstSession.messages || []
        await scrollToBottom()
      }
    }
  } catch (error) {
    console.error(':', error)
    showToast('')
  }
}

// 
const switchSession = async (session) => {
  if (currentSession.value === session.id) return  // 
  
  currentSession.value = session.id
  currentSessionTitle.value = session.title
  messages.value = session.messages || []  // 
  await scrollToBottom()
}

// 
const formatSessionTitle = (session) => {
  if (session.title && session.title !== '') {
    return session.title
  }
  return ` ${new Date(session.createTime).toLocaleString('zh-CN', {
    month: 'numeric',
    day: 'numeric',
    hour: 'numeric',
    minute: 'numeric'
  })}`
}

// 
const sendMessage = async () => {
  if (!inputMessage.value.trim() || isLoading.value) return
  
  // 
  if (!currentSession.value) {
    await createNewSession()
    if (!currentSession.value) return
  }

  const userMessage = inputMessage.value.trim()
  messages.value.push({
    role: 'user',
    content: userMessage
  })
  inputMessage.value = ''
  await scrollToBottom()

  isLoading.value = true
  try {
    const res = await http.post(`/api/v1/chats/${CHAT_ID}/completions`, {
      session_id: currentSession.value,
      question: userMessage
    })

    //  402 
    if (res.data.code === 402) {
      showToast(res.data.data.message || '')
      await router.push('/payment')
      isLoading.value = false
      return
    }

    if (res.data.code === 0) {
      //  currentAnswer
      currentAnswer.value = res.data
      
      // 
      messages.value.push({
        role: 'assistant',
        content: res.data.data.data.answer || ''
      })
    } else {
      showToast(res.data.message || '')
    }
  } catch (error) {
    console.error(':', error)
    showToast('')
  } finally {
    isLoading.value = false
    await scrollToBottom()
  }
}

// 
const scrollToBottom = async () => {
  await nextTick()
  if (messageList.value) {
    messageList.value.scrollTop = messageList.value.scrollHeight
  }
}

//  onMounted
onMounted(async () => {
  await getSessionList()  // 
})

//  createNewSession
const createNewSession = async () => {
  try {
    const res = await http.post(`/api/v1/chats/${CHAT_ID}/sessions`)
    if (res.data.code === 0) {
      const sessionData = res.data.data.data
      const newSession = {
        id: sessionData.id,
        title: sessionData.name,
        createTime: sessionData.create_time
      }
      sessions.value.unshift(newSession)  // 
      currentSession.value = newSession.id
      currentSessionTitle.value = newSession.title
      
      // 
      if (sessionData.messages && sessionData.messages.length > 0) {
        messages.value = sessionData.messages
      } else {
        messages.value = []
      }
      
      await scrollToBottom()
    } else {
      showToast('')
    }
  } catch (error) {
    console.error(':', error)
    showToast('')
  }
}

// 
const confirmDelete = (session) => {
  showDialog({
    title: '',
    message: '',
    showCancelButton: true,
  }).then(() => {
    deleteSession(session)
  }).catch(() => {
    // 
  })
}

// 
const deleteSession = async (session) => {
  try {
    const res = await http.delete(`/api/v1/chats/${CHAT_ID}/sessionsDel`, {
      data: {
        ids: [session.id]
      }
    })
    
    if (res.data.code === 0) {
      // 
      sessions.value = sessions.value.filter(s => s.id !== session.id)
      showToast('')
      
      // 
      if (currentSession.value === session.id) {
        if (sessions.value.length > 0) {
          await switchSession(sessions.value[0])
        } else {
          currentSession.value = null
          currentSessionTitle.value = ''
          messages.value = []
        }
      }
    } else {
      showToast('')
    }
  } catch (error) {
    console.error(':', error)
    showToast('')
  }
}

//  markdown-it
const md = new MarkdownIt({
  html: true,
  linkify: true,
  typographer: true,
  highlight: function (str, lang) {
    if (lang && hljs.getLanguage(lang)) {
      try {
        return hljs.highlight(str, { language: lang }).value
      } catch (error) {
        console.warn(':', error)
        return str
      }
    }
    return str
  }
})

//  Markdown 
const renderMarkdown = (content) => {
  return content ? md.render(content) : ''
}
</script>

